.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.maintable {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.dragdroptable {
  width: auto;
  display: flex;  
  justify-content: center;
  padding: 20px;
  background-color: rgb(255, 255, 255);
  border-radius: 5px;
  box-shadow: 0px 7px 6px #00000029;
  padding-left: 10%;
  padding-right: 10%;
}



.sentence1 {
  width: auto;
  display: flex;  
  justify-content: center;
  padding: 5px;
  background-color: rgb(238, 238, 238);
  /* border-radius: 5px; */
  /* box-shadow: 0px 7px 6px #00000029; */
  /* padding-left: 10%;
  padding-right: 10%; */
  padding-left: 20px;
  padding-right: 20px;
  margin: 8px;
  
}

.tblcellcolor{
  background-color: #F5D38B;
  width: 120px;
  height: 30px;
  text-align: center;
  background: #F5D38B 0% 0% no-repeat padding-box;
  box-shadow: 0px 3px 6px #00000029;
  border-radius: 5px;
  opacity: 1;
  color: #412410;

}


